

























import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component({
  name: 'RuleList'
})
export default class extends Vue {
  @Prop({ default: () => [{
    rule: '',
    message: ''
  }] }) private rules: any;

  private add () {
    this.rules.push({
      rule: '',
      message: ''
    })
  }

  private deleteItem (index) {
    this.rules.splice(index, 1);
  }
}
